import Hapi from '@hapi/hapi'
import Inert from '@hapi/inert' //
import SocketIO from 'socket.io'
import keys from 'lodash/keys'
import genPm from 'wsemi/src/genPm.mjs'
import haskey from 'wsemi/src/haskey.mjs'
import getdtvstr from 'wsemi/src/getdtvstr.mjs'
import getdtv from 'wsemi/src/getdtv.mjs'
import j2o from 'wsemi/src/j2o.mjs'
import isfun from 'wsemi/src/isfun.mjs'
import arrhas from 'wsemi/src/arrhas.mjs'


/**
 * socket.io
 *
 * @param {Object} opt 
 * @param {Object} [opt.serverHapi={}] hapiapiroutehapinginxapi
 * @param {Integer} [opt.port=8080] hapisocket.ioporthapisocket.io8080
 * @param {Function} opt.authenticate tokenPromiseresolve(true)resolve(false)
 * @param {Object} [opt.funcs={}] keyvaluePromiseresolvereject{}
 * @param {Array} [opt.routes=[]] routes[]
 * @example
 *
 * import SiServer from 'w-comor-socketio/dist/si-server.umd.js'
 *
 * function random(min, max) {
 *     return Math.floor(Math.random() * max) + min
 * }
 *
 * let opt = {
 *     port: 8080,
 *     authenticate: function(token) {
 *         //token
 *         return new Promise(function(resolve, reject) {
 *             setTimeout(function() {
 *                 resolve(true)
 *             }, 1000)
 *         })
 *     },
 *     filterFuncs: function(token, funcs) {
 *         //tokenfuncs
 *         return new Promise(function(resolve, reject) {
 *             funcs = funcs.filter(function(v) {
 *                 return v.indexOf('Hide') < 0
 *             })
 *             resolve(funcs)
 *         })
 *     },
 *     onClientChange: function(clients, opt) {
 *         console.log(`Server[port:${opt.port}] now clients: ${clients.length}`)
 *     },
 *     funcs: {
 *         'group.plus': function({ p1, p2 }) {
 *             return new Promise(function(resolve, reject) {
 *                 setTimeout(function() {
 *                     resolve(p1 * p2)
 *                 }, random(100, 3000))
 *             })
 *         },
 *         'group.div': function({ p1, p2 }) {
 *             return new Promise(function(resolve, reject) {
 *                 setTimeout(function() {
 *                     resolve(p1 / p2)
 *                 }, random(100, 3000))
 *             })
 *         },
 *         'add': function({ p1, p2 }) {
 *             return new Promise(function(resolve, reject) {
 *                 setTimeout(function() {
 *                     resolve(p1 + p2)
 *                 }, random(100, 3000))
 *             })
 *         },
 *         'addHide': function({ p1, p2 }) {
 *             return new Promise(function(resolve, reject) {
 *                 setTimeout(function() {
 *                     resolve(p1 + p2)
 *                 }, random(100, 3000))
 *             })
 *         },
 *         'minu': function({ p1, p2 }) {
 *             return new Promise(function(resolve, reject) {
 *                 setTimeout(function() {
 *                     resolve(p1 - p2)
 *                 }, random(100, 3000))
 *             })
 *         },
 *     },
 * }
 *
 * new SiServer(opt)
 *
 */
function SiServer(opt) {


    //default
    if (!opt.port) {
        opt.port = 8080
    }


    //funcs
    let funcs = []
    if (haskey(opt, 'funcs')) {
        funcs = keys(opt['funcs'])
    }


    //authenticate
    function authenticate(token) {
        let pm = genPm()
        if (isfun(opt.authenticate)) {
            opt.authenticate(token)
                .then(function(vd) {
                    pm.resolve(vd)
                })
        }
        else {
            pm.resolve(true)
        }
        return pm
    }


    //server
    let server
    if (opt.serverHapi) {

        //use serverHapi
        server = opt.serverHapi

    }
    else {

        //create server
        server = Hapi.server({
            port: opt.port,
            //host: 'localhost',
            routes: {
                cors: true
            },
        })

    }


    //io
    let io = null
    try {
        io = SocketIO(server.listener)
    }
    catch (err) {
        console.log(`create SocketIO catch:`, err)
        return null
    }


    //connect
    let clients = []
    io.on('connect', function(client) {
        //console.log('connect', client.id)


        //push
        clients.push(client.id)
        if (isfun(opt.onClientChange)) {
            opt.onClientChange(clients, opt)
        }


        //execFunction
        async function execFunction(data) {
            //console.log('execFunction', client.id, data)

            //token
            let token = getdtvstr(data, 'token')

            //vd
            let vd = await authenticate(token)

            //check
            if (vd) {

                //func
                let func = getdtvstr(data, 'func')

                //input
                let input = getdtv(data, 'input')

                //getFuncs
                if (func === 'getFuncs') {

                    if (isfun(opt.filterFuncs)) {
                        funcs = await opt.filterFuncs(token, funcs)
                    }

                    //add output
                    data['output'] = { sys: 'sys', funcs: funcs }

                }
                //call
                else if (arrhas(funcs, func)) {

                    //call func in opt.funcs
                    let output = await opt['funcs'][func](input)

                    //add output
                    data['output'] = output

                }
                else {

                    //add output
                    data['output'] = { err: `can not find: ${func}` }

                }

            }
            else {

                //add output
                data['output'] = { err: `can not authenticate token: ${token}` }

            }

            //delete input, inputinput
            delete data['input']

            //send
            try {
                client.send(JSON.stringify(data), function(err) {
                    if (err) {
                        console.log(`Server: send output error:`, err)
                    }
                })
            }
            catch (err) {
                console.log(`Server: send output catch:`, err)
            }

        }


        //message
        client.on('message', function(msg) {
            //console.log('message', client.id, msg)

            //data
            let data = j2o(msg)

            //execFunction
            execFunction(data)

        })


        //disconnect, close
        client.on('disconnect', function(msg) {
            //console.log('disconnect', client.id, msg)

            //client
            clients = clients.filter(function(id) {
                return id !== client.id
            })
            if (isfun(opt.onClientChange)) {
                opt.onClientChange(clients, opt)
            }

        })


    })


    async function createServer() {

        if (!opt.serverHapi) {

            //register inert
            await server.register(Inert)

            //api
            let api = [
                {
                    method: 'GET',
                    path: '/{file*}',
                    handler: {
                        directory: {
                            path: './'
                        }
                    },
                }
            ]

            //route
            server.route(api)

        }

        //start
        await server.start()

        console.log(`Server running at: ${server.info.uri}`)

    }
    createServer()


}


export default SiServer
